import mapboxgl from 'mapbox-gl'
import StylePicker from './StylePicker.vue'
import {
  dataSources
} from '../config/datalayers-config.js'

export default {
  name: 'map-component',
  data: () => {
    return {
      map: null,
      table_properties: {
        'hoi ': 'checking'
      },
      kabelpopup: false,
      mapLoaded: false
    }
  },
  components: {
    'v-style-picker': StylePicker
  },
  mounted () {
    this.map = this.$refs.map.map
    this.$store.commit('setMap', this.map)

    var datapopup = new mapboxgl.Popup({
      closeButton: false,
      closeOnClick: false
    })

    datapopup.setDOMContent(document.getElementById('popup-table'))

    // wait for the map to load
    this.map.on('load', () => {
      this.mapLoaded = true
      this.map.setMaxBounds([
        [3, 51],
        [7, 54]
      ])
      this.map.addControl(new mapboxgl.NavigationControl())
      this.map.addSource(dataSources.name, dataSources.sources)
      this.$store.state.dataLayers.forEach((layer) => {
        layer['mapbox-layers'].forEach((maplayer) => {
          this.map.addLayer(maplayer)
        })
      })

      this.map.on('mousemove', (e) => {
        var features = this.map.queryRenderedFeatures(e.point)
        this.map.getCanvas().style.cursor = ''
        this.map.setFilter('bodemverandering-hover', ['==', 'Id', ''])
        this.map.setFilter('kabels-hover', ['==', 'id', ''])
        this.map.setFilter('vaste-lagen-hover', ['==', 'OBJECTID', ''])
        this.table_properties = {}

        datapopup.remove()
        if (features.length === 0) return
        if (features[0].layer.id === 'kabels') {
          this.map.getCanvas().style.cursor = 'pointer'
          this.map.setFilter('kabels-hover', ['==', 'id', features[0].properties.id])
          this.table_properties['Beheer'] = features[0].properties.K_L_beh
          this.table_properties['Object Id'] = features[0].properties.id
          // this.table_properties = e.features[0].properties
        } else if (features[0].layer.id === 'bodemverandering') {
          this.map.getCanvas().style.cursor = 'pointer'
          this.map.setFilter('bodemverandering-hover', ['==', 'Id', features[0].properties.Id])
          this.table_properties['Bodemverandering [cm/jaar]'] = features[0].properties.Bodemveran.toFixed(5)
          this.table_properties['Oppervlakte [m2]'] = features[0].properties.area
          this.table_properties['Volume [m3]'] = features[0].properties.volume
        } else if (features[0].layer.id === 'vaste-lagen') {
          this.map.getCanvas().style.cursor = 'pointer'
          this.table_properties = features[0].properties
          this.map.setFilter('vaste-lagen-hover', ['==', 'OBJECTID', features[0].properties.OBJECTID])
        } else return
        datapopup.setLngLat(e.lngLat)
          .addTo(this.map)
      })
    })
  }
}
