export default {
  name: 'style-picker',
  data: function() {
    return {
      id: this._uid,
      satelliteBool: true,
      mapLoaded: false ,
      satelliteSwitch: 1

    }
  },
  mounted() {
    // check optional props
    this.mapstyles = this.mapboxstyles || this.mapstyles
    this.mapstyle = this.mapboxstyle || this.mapstyle
  },
  methods: {
    deferredMountedTo(map) {
      this.$store.state.map.addControl(this, 'bottom-right');
    },
    onAdd(map) {
      // return containing div
      return this.$refs[this.id]
    },
    onRemove() {
      return null
    },
    // satelliteSwitch() {
    //   console.log(satelliteBool)
    //   if (this.satelliteBool) {
    //     this.$store.state.map.setStyle('mapbox://styles/mapbox/light-v9', true)
    //   } else {
    //     this.$store.state.map.setStyle('mapbox://styles/mapbox/dark-v9', true)
    //   }
    // },
    switchSatellite() {
      if (this.satelliteSwitch === 1) {
        this.satelliteSwitch = 0
      } else {
        this.satelliteSwitch = 1
      }
      this.map.setPaintProperty('satellite', 'raster-opacity', this.satelliteSwitch);
    }
  }
}
